.StatFull{
    display: flex;
    flex-direction: row;
    gap: 30px;
    flex-wrap: wrap;
    justify-content: space-around;
}

.StatDash{
    width: 200px;
    text-align: center;
    padding: 20px;
    align-content: space-between;
    border-radius: 10px;
    box-shadow: 10px 10px 40px rgba(78, 91, 105, 0.314);
}

.InfoWin{
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: center;
    padding: 30px;
    border-radius: 10px;
    height: 100px;
    border: 2px solid black
}

.InfoWin .Qua{
    font-size: 40px;
    font-weight: 900;
   
}