.exemple {
  table-layout: auto;
  width: 100%;

  text-align: left;
  border: 1px rgb(255, 255, 255) solid;
}

tbody {
}

.SpareSetList img {
  width: 40px;
  height: 40px;
  border-radius: 100%;
}

tr {
}

td {
  background-color: rgb(255, 255, 255);

  line-height: 30px;
}

input {
  text-overflow: ellipsis;
  word-wrap: break-word;
  border: none;
  margin: 0;
  padding-top: 0px;
  background-color: rgb(245, 245, 245);
}

input:focus {
  background-color: rgb(255, 255, 255);
}
.Pwd input {
  background-color: rgb(49, 49, 49);
}

.Pwd:hover input {
  background-color: #7b241c;
  color: white;
}

.Pwd:hover input::placeholder {
  color: white;
}

.Actifa::placeholder {
  color: rgb(255, 255, 255);
}

[data-status="Actif"] {
  background-color: #25d46c;
}

[data-status="Holiday"] {
  background-color: #ec7063;
}
[data-status="Off"] {
  background-color: #3498d8;
}

.Imagee {
  box-shadow: 0px 0px 8px rgb(109, 109, 109);
}
thead {
  background-color: rgb(255, 255, 255);
  line-height: 40px;
}

input::placeholder {
  color: rgb(81, 81, 81);

  font-family:'Times New Roman', Times, serif
}
