  color: white;
}

.Alert, .Info, .AllInfo {
  display: flex;
  align-items: center;
  justify-content: center;

  color: rgb(255, 255, 255);
  background-color: rgb(255, 0, 0);
  border-radius: 100%;
  width: 30px;
  height: 30px;
  margin-right: 5px;
}

.Info{
  background-color: rgb(0, 138, 32);
}

.AllInfo{
  background-color: rgb(10, 0, 43);
}


.Alert p {
  text-align: center;
}

.headerPlus {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;


  align-items: center;
  margin-top: 50px;

}
.FullHeader{
  margin-bottom: 50px;
}


.Header{
  margin: 0;
  
}

.ColoIcon{
  
  width: 20px;
  height: 20px;
  -webkit-filter: brightness(0) saturate(100%) invert(100%) sepia(28%) saturate(2%) hue-rotate(68deg) brightness(109%) contrast(101%);
          filter: brightness(0) saturate(100%) invert(100%) sepia(28%) saturate(2%) hue-rotate(68deg) brightness(109%) contrast(101%);
  
}

.FrMenu{
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  margin-bottom: 10px;
  margin-top: 20px;
  
}
:root {
  --ColorSecondary: #7051f1;
  --BoxShadow: 2px 2px 8px rgba(0, 0, 0, 0.511);
  --fontsizesecondary: 17px;
}
.Tab {
  padding-top: 50px;
  padding-inline: 50px;
  padding-bottom: 50px;
}

.App-link {
  color: #61dafb;
}

:root {
  --size_menu: 17px;
  --color-menu: white;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.Colonne {
  display: flex;
  flex-direction: row;
}

/* Menu */
.Menu {
  background-color: #272a37;
  width: 12%;

  padding-inline: 10px;

  height: 100vh;
  position: fixed;
}

.list {
  color: rgb(255, 255, 255);
  text-decoration: none;
  margin-bottom: 15px;
  font-weight: 900;
  font-size: 17px;
  font-size: var(--size_menu);
}

.ProfilHeader {
  display: flex;
  flex-direction: column;
  color: white;
  color: var(--color-menu);
  padding-left: 10px;
}
/* Content */

.Content {
  width: 100%;
  margin-left: 12%;


  min-height: 100vh;
}

